import React, { useState, useEffect } from "react";

const ScrollToTopHeader = () => {
  // tat pour savoir si le bouton doit tre visible
  const [isVisible, setIsVisible] = useState(false);

  // Vrifier la position du scroll
  const checkScrollPosition = () => {
    if (window.scrollY > 300) {
      setIsVisible(true); // Afficher le bouton aprs un certain dfilement
    } else {
      setIsVisible(false); // Masquer le bouton si on est en haut
    }
  };

  // Ajouter un couteur d'vnements pour surveiller le dfilement
  useEffect(() => {
    window.addEventListener("scroll", checkScrollPosition);
    return () => {
      window.removeEventListener("scroll", checkScrollPosition);
    };
  }, []);

  // Fonction de dfilement vers le haut
  const scrollToTop = () => {
    window.scrollTo({
      top: 0,
      behavior: "smooth", // Dfilement fluide
    });
  };

  return (
    <>
      {/* Bouton de retour en haut */}
      {isVisible && (
        <button
          onClick={scrollToTop}
          style={{
            position: "fixed",
            bottom: "20px",
            right: "20px",
            padding: "10px 20px",
            backgroundColor: "#007bff", // Couleur bleu de Bootstrap (ou vous pouvez remplacer par une autre nuance)
            color: "#fff",
            border: "none",
            borderRadius: "50%",
            fontSize: "20px",
            cursor: "pointer",
            boxShadow: "0 4px 8px rgba(0, 0, 0, 0.3)",
            zIndex: 1000,
          }}
        >
          
        </button>
      )}
    </>
  );
};

export default ScrollToTopHeader;
