import CryptoJS from "crypto-js";

/**
 * Fonction pour chiffrer un message
 * @param {string} message - Le message  chiffrer
 * @returns {string} - Le message chiffr
 */
export function cryptage(message:string) {
  if (!message) {
    throw new Error("Le message doit tre fourni.");
  }
  const cle = "ma-cle-secrete"; // Cl par dfaut
  return CryptoJS.AES.encrypt(message, cle).toString();
}

/**
 * Fonction pour dchiffrer un message
 * @param {string} messageCrypte - Le message chiffr
 * @returns {string} - Le message dchiffr
 */
export function decryptage(messageCrypte:string) {
  if (!messageCrypte) {
    throw new Error("Le message chiffr doit tre fourni.");
  }
  const cle = "ma-cle-secrete"; // Cl par dfaut
  const bytes = CryptoJS.AES.decrypt(messageCrypte, cle);
  return bytes.toString(CryptoJS.enc.Utf8);
}


